.keybase {
    background-color: #333;
    cursor: pointer;
    border: none;
    border-radius: 3px;
    color: white;
  
    display: flex;
    align-items: center;
    justify-content: center;
  }
  

  .keyboardContainer {
    display: flex;
    flex-wrap: wrap;
    width: 450px;
    margin: 0 auto;
    gap: 5px;
    margin-top: 50px;
  }
  .key {
    background-color: rgb(211, 214, 218);
    display: flex;
    justify-content: center;
    cursor: pointer;
    border: none;
    border-radius: 3px;
    color: black;
    font-size: .875rem;
    line-height: 1.25rem;
    font-weight: 700;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 50px;
  }
  .emptyKey {
    width: 25px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .enterKey {
